.navbar {
    position: fixed;
    bottom: 0;
    width: 100%;
    text-align: center;
    padding: 1vw 0;
}

.name {
    font-size: 6.5em;
    margin-top: 110% !important;
    margin-left: 3vw !important;
    font-weight: 400;
    margin: 0;
    cursor: pointer;
    position: relative;
    text-align: right;
}

.navbar-container {
    position: fixed;
    left: 1vw;
    top: 2vh;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: margin 0.5s ease-in-out;
    z-index: 10;
}

.navbar-links {
    font-size: 1.5em;
    text-decoration: none;
    color: black;
    display: flex;
    flex-direction: column;
    margin-left: 74% !important;
    margin-top: 113% !important;
    position: absolute;
    text-align: right;
}
